@import "../mixins";
.footer-menu{
  background-color: $black;
  color: #fff;
  .menu{
    padding: 2.5em 0;
    border-top: 1px dashed rgba(#FFFFFF,.1);
  }
}

.footer-nav{
  padding: 2.5em 0;
  @include grid100gap(1.5em);
  &__links{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: -1em;
  }
  &__link{
    margin: 1em 1.5em 0 0;
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
}

.footer-bottom{
  padding: 2.5em 0;
  border-top: 1px dashed rgba(#FFFFFF,.1);
  display: flex;
  &__item{
    margin: 0 2.5em 0 0;
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
}

.menu{
  display: grid;
  grid-template-columns: repeat(6,1fr);
  gap: 1.75em;
  grid-auto-flow: dense;
  &__group{
    &._big{
      grid-row: 1/3;
    }
  }
  &__title{
    font-weight: 700;
    font-size: 1.125em;
    line-height: 122%;
  }
  &__bottom{
    margin-top: 1em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    &:not(:last-child){
      margin-bottom: .25em;
    }
    span{
      font-size: .875em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer-nav{
    display: none;
  }

  .footer-menu{
    .menu{
      display: none;
    }
  }

  .footer-bottom{
    padding: 1.5em 0;
    flex-direction: column;
    align-items: flex-start;
    &__item{
      margin: 0;
      &:not(:last-child){
        margin-bottom: 1em;
      }
    }
  }

  .menu{
    grid-template-columns: 100%;
    gap: 0;
    &__group{
      &._big{
        grid-row: unset;
      }
    }
    &__top{
      padding-left: 1.5em;
    }
    &__bottom{
      display: none;
      margin: 0;
    }
    &__link{
      padding-left: 3em;
      span{
        font-size: 1em;
      }
    }
  }
}
