@import "../mixins";
.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    margin-bottom: .625em;
  }
  textarea,
  input {
    border: none;
    background: #FFFFFF;
    border-radius: .375em;
    padding: 0 1.375em;
    height: 3.9375em;
    width: 100%;
    font-size: 1em;
    color: $black;
    &::placeholder {
      color: rgba($black,.4);
    }
    &:focus{
      box-shadow: 0 0 .5em .125em rgba($primary,.4);
    }
  }
  textarea{
    padding-top: 1.375em;
    padding-bottom: 1.375em;
    height: 7.5em;
    resize: none;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-color: $red;
        border-color: $red;
        background-image: url(../img/check.svg);
      }
    }
  }
  label {
    display: flex;
    cursor: pointer;
    font-size: 1em;
    line-height: 130%;
    &:before {
      content: '';
      z-index: 1;
      flex-shrink: 0;
      margin-right: .8125em;
      border-radius: .125em;
      border: 1px solid $gray;
      width: 1.125em;
      height: 1.125em;
      transition: all .100s;
      @include bgImgSize(.875em);
      transform: translateY(.0625em);
    }
  }
  input[type='radio']{
    &+label{
      &:before{
        border-radius: 50%;
        background-size: .625em;
      }
    }
    &:checked+label:before{
      border-color: $gray;
      background-color: transparent;
      background-image: url(../img/radio.svg);
    }
  }
  a{
    position: relative;
    z-index: 2;
    color: $red;
    &:hover{
      text-decoration: underline;
    }
  }
  &._small{
    label{
      span{
        font-size: .8125em;
        line-height: 130%;
      }
    }
  }
  &._green{
    input:checked+label:before{
      border-color: $primary;
      background-color: $primary;
    }
    a{
      color: $primary;
    }
  }
}

.cartcalc{
  display: grid;
  grid-template-columns: 1.25em calc(100% - 2.5em) 1.25em;
  align-items: center;
  svg{
    width: 100%;
    height: auto;
  }
  input{
    border: none;
    background-color: transparent;
    font-weight: 600;
    font-size: 2.5em;
    height: 1.5em;
    width: 100%;
    text-align: center;
  }
  .ccalc-plus{
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  .ccalc-minus{
    &:hover{
      path{
        stroke: $red;
      }
    }
  }
}
