@import 'vars';
@import "mixins";
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
._section{
  padding: 5em 0;
  &.pt0{
    padding-top: 0;
  }
}

._section2{
  padding: 2.5em 0;
}

.info-block{
  @include grid100gap(.5em);
}

.grid-column2{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .875em));
  gap: 1.75em;
}

.grid-column3{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 3.5em / 3));
  gap: 1.75em;
}

.grid-column4{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 1.75em * 3 / 4));
  gap: 1.75em;
}

.swiper-pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  &-bullet{
    width: .75em;
    height: .75em;
    margin: 0 .5em !important;
    border: 1px solid $red;
    opacity: 1;
    background-color: transparent;
    &-active{
      background-color: $red;
    }
  }
  &._white{
    .swiper-pagination-bullet{
      border-color: #fff;
      &-active{
        background-color: #fff;
      }
    }
  }
}

.tag{
  display: flex;
  align-items: center;
  padding: 0 .75em;
  height: 1.75em;
  border-radius: 2em;
  background: #F4F5F8;
  font-weight: 500;
  margin: .25em .25em 0 0;
  color: $gray2;
  span{
    font-size: .8125em;
    line-height: 123%;
  }
}

.tabs{
  margin-top: 2.5em;
  &__select{
    display: none;
  }
  &__options{
    display: flex;
    flex-wrap: wrap;
    margin-top: -1em;
  }
  &__content{
    margin-top: 2.5em;
  }
}

.tab{
  display: flex;
  align-items: center;
  margin: 1em 1em 0 0;
  padding: 0 1.5em;
  height: 3.4375em;
  border-radius: .375em;
  border: 1px solid $red;
  flex-shrink: 0;
  cursor: pointer;
  transition: all .3s;
  span{
    font-size: 1em;
    line-height: 120%;
  }
  &:hover,
  &._active{
    background-color: $red;
    color: #fff;
  }
  &._active{
    pointer-events: none;
  }
  &-content{
    display: none;
    &._active{
      display: block;
    }
  }
}

.gallery{
  @media screen and (min-width: 577px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 3em / 4));
        gap: 1em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
        &:first-child{
          grid-column: 1/-1;
        }
      }
      &-pagination{
        display: none;
      }
    }
  }
  &__img{
    display: block;
    position: relative;
    &-wrap{
      @include imgCover(57%);
      img{
        border-radius: .3125em;
      }
    }
  }
}

.video,
.gallery__img._video,
.carousel__slide.has-html5video{
  &:before{
    content: '';
    z-index: 2;
    @include centerAbsolute;
    width: 2.5em;
    height: 2.5em;
    background-image: url(../img/play.svg);
    @include bgImgSize(100%);
  }
}

.video{
  display: block;
  position: relative;
  &__img{
    display: block;
    width: 100%;
    img{
      width: 100%;
      border-radius: .375em;
    }
  }
}

@media screen and (min-width: 993px){
  ._hide-desktop{
    display: none;
  }
}


@media screen and (max-width: 992px){
  ._section{
    padding: 2em 0;
  }

  ._section2{
    padding: 1em 0;
  }

  .grid-column4,
  .grid-column3{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }

  .grid-column2{
    grid-template-columns: 100%;
    gap: 1.5em;
  }

  ._mob-hide{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .grid-column3,
  .grid-column4{
    grid-template-columns: 100%;
    gap: .5em;
  }

  .tabs{
    position: relative;
    margin-top: 1.5em;
    &__select{
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-height: 3.75em;
      border-radius: .375em;
      padding: .5em 1.5em;
      transition: all .3s;
      img{
        width: 1.5em;
        display: block;
        flex-shrink: 0;
        margin-left: 1em;
        transition: inherit;
      }
      span{
        font-size: 1em;
        line-height: 120%;
      }
      &._active{
        &~.tabs__options{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__options{
      position: absolute;
      width: 100%;
      top: 100%;
      left: 0;
      margin-top: .25em;
      border: 1px solid $red;
      border-radius: .375em;
      overflow: auto;
      max-height: 13.75em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      z-index: 5;
      background-color: #fff;
    }
    &__content{
      margin-top: 1.5em;
    }
  }

  .tab{
    width: 100%;
    margin: 0;
    border-radius: 0;
    background-color: transparent;
    border: none;
    &:not(:last-child){
      border-bottom: 1px solid $red;
    }
  }

  .gallery{
    overflow: hidden;
    .swiper{
      &-pagination{
        position: static;
        margin-top: 1em;
      }
    }
  }
}
