@import 'vars';
html {
    width: 100%;
    font-size: 1920px;
    @media screen and (max-width: 1920px){
        font-size: 100vw;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.page {
    padding-top: 3.4375em;
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: 0.833334%;
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
    }
}

.container {
    max-width: 1920px;
    width: 100%;
    margin: 0 auto;
    padding: 0 20.375em;
}

a {
    &:hover {
        color: $red;
    }
}


@media screen and (max-width: 992px){
    .page{
        font-size: 2.2%;
        padding: 0;
    }

    .container{
        padding: 0 1em;
    }
}


@media screen and (max-width: 576px){
    .page{
        font-size: 4.44445%;
    }
}
