@import "../mixins";
.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding-top: 3.4375em;
  &__inner{
    display: flex;
    align-items: center;
    padding: 1.25em 0;
  }
  &__logo{
    display: block;
    width: 7.75em;
    flex-shrink: 0;
    margin-right: 2em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin-left: auto;
    padding: 0 1.625em;
    height: 3.4375em;
  }
  &__time{
    display: none;
  }
}

.close-btn{
  margin-left: auto;
  display: flex;
  align-items: center;
  color: $red;
  svg{
    width: 1.5em;
    height: auto;
    margin-left: .5em;
  }
  span{
    font-size: 1em;
    line-height: 130%;
  }
}

.mob-menu{
  display: flex;
  align-items: center;
  flex-grow: 1;
  &__close{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__btn,
  &__link{
    font-weight: 600;
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__link{
    margin-right: 2.5em;
  }
  &__btn{
    margin-right: 1.25em;
    height: 3.75em;
    padding: 0 1.5em;
    border: 1px solid #DEE2E9;
    border-radius: .375em;
    img{
      width: 1.5em;
      display: block;
      flex-shrink: 0;
      margin-right: .625em;
    }
    &:hover{
      background-color: rgba($primary,.2);
      border-color: rgba($primary,.2);
    }
  }
}

.header-top{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: $black;
  color: #fff;
  &__inner{
    height: 3.4375em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.nav2{
  display: flex;
  align-items: center;
  &__link{
    margin-right: 2em;
    color: rgba(#fff,.6);
    &:hover{
      color: #fff;
    }
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
}

.header-contacts{
  display: flex;
  align-items: center;
  &__item{
    display: flex;
    align-items: center;
    margin-left: 2em;
    img{
      width: 1.9375em;
      display: block;
      flex-shrink: 0;
      margin-right: .75em;
    }
    span{
      font-size: 1em;
      line-height: 120%;
    }
    &:hover{
      color: $primary;
    }
  }
}

.header-catalog{
  @media screen and (min-width: 993px){
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 100;
    background-color: #fff;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    &._active{
      opacity: 1;
      pointer-events: unset;
    }
    &>.container{
      padding-bottom: 2.5em;
      padding-top: 2.5em;
    }
  }
  &__close{
    margin-right: 2em;
    position: relative;
    z-index: 2;
  }
  .menu{
    margin-top: 2em;
  }
  .footer-nav{
    margin-top: -1.5em;
    padding: 0 0 2.5em;
    border-bottom: 1px dashed rgba($black,.1);
  }
}

.catalog-contacts{
  margin-top: auto;
  padding: 3em 0;
  background-color: $black;
  color: #fff;
  &__inner{
    display: flex;
  }
  .header-contacts__item{
    color: #fff;
    span{
      font-size: 2.25em;
      font-weight: 700;
    }
    &:hover{
      color: $primary;
    }
  }
  .header__time{
    @include grid100gap(.625em);
    margin-left: 2.8125em;
  }
  .header__btn{
    width: 20.375em;
  }
}

.mob-buttons{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    position: static;
    padding: 0;
    &__inner{
      padding: 1em 0;
    }
    &__logo{
      width: 4.5625em;
    }
    &__btn{
      width: 100%;
      margin: 1em 0 0;
    }
    &__time{
      @include grid100gap(.625em);
      margin-top: 1em;
      .text16{
        font-size: 1em;
      }
    }
    &._open{
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .mob-buttons{
    display: flex;
    align-items: center;
    margin-left: auto;
    .consult__social{
      margin-left: 1em;
    }
  }

  .burger{
    margin-left: 1.5em;
    width: 1.5em;
    img{
      width: 100%;
      display: block;
    }
  }

  .mob-menu{
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    background-color: #fff;
    display: block;
    padding: 2.25em 1em;
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__close{
      display: flex;
    }
  }

  .header-top{
    position: static;
    background-color: transparent;
    &__inner{
      height: unset;
      display: block;
    }
    .container{
      padding: 0;
    }
  }

  .nav,
  .nav2{
    flex-direction: column;
    align-items: flex-start;
    &__btn{
      padding: 0;
      border: none;
      border-radius: 0;
      justify-content: space-between;
      img{
        display: none;
      }
      &:hover{
        background-color: transparent;
        border-color: $gray;
      }
    }
  }

  .nav__btn,
  .nav__link,
  .nav2__link,
  .menu__link,
  .menu__top{
    padding: 1em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: $border;
    width: 100%;
    color: $black;
    span{
      font-weight: 400;
      font-size: 1em;
      line-height: 150%;
    }
  }

  .nav__btn,
  .menu__top{
    &:after{
      content: '';
      width: 1.5em;
      height: 1.5em;
      display: block;
      flex-shrink: 0;
      margin-left: 1em;
      background-image: url(../img/arrow-bottom2.svg);
      @include bgImgSize(100%);
      transition: all .3s;
    }
    &._active{
      color: $red;
      &:after{
        background-image: url(../img/arrow-top.svg);
      }
    }
  }

  .header-catalog{
    //display: block;
    display: none;
    padding: 0;
    width: 100%;
    position: static;
    .container{
      padding: 0;
    }
    .menu{
      padding: 0;
      margin: 0;
    }
    &__close{
      display: none;
    }
  }

  .header-contacts{
    margin-top: 1em;
    ._mob-hide{
      display: none;
    }
    &__item{
      margin: 0;
      color: $black;
      span{
        font-size: 2.25em;
        font-weight: 700;
      }
    }
  }

  .catalog-contacts{
    display: none;
  }
}
