@import "../mixins";
.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    &__bg{
        background: rgba(0, 0, 0, 0.66);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &--active{
        opacity: 1;
        pointer-events: unset;
        .modal__bg{
            display: block;
        }
    }
    &__dialog{
        background: #F6F7F9;
        border-radius: .375em;
        width: 39.6875em;
        max-width: 100%;
        overflow-y: auto;
        max-height: 100vh;
        padding: 2.5em;
        position: relative;
        z-index: 3;
    }
    .close-btn{
        margin-bottom: 1.25em;
        position: relative;
        z-index: 3;
        background-color: transparent;
    }
    .calc-info{
        padding-top: 1.25em;
        &__form{
            gap: 1.25em;
            @media screen and (min-width: 577px){
                grid-template-columns: 1fr 1fr;
            }
        }
        &__btn{
            align-self: flex-end;
        }
    }
}

.same-modal{
    .modal__dialog{
        width: 92em;
    }
    &__grid{
        display: grid;
        grid-template-columns: 35em calc(100% - 37em);
        gap: 2em;
    }
    &__calc{
        margin-top: -2.75em;
    }
    .calc__groups{
        margin-top: 1.25em;
        gap: 1.25em;
    }
}

.same-card{
    display: block;
    position: relative;
    &__img{
        @include imgCover(96.75%);
        img{
            border-radius: .375em;
        }
        &:before{
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            background: linear-gradient(0deg, rgba(34, 34, 34, 0.5), rgba(34, 34, 34, 0.5));
            left: 0;
            top: 0;
            z-index: 2;
            border-radius: .375em;
        }
    }
    &__content{
        position: absolute;
        padding: 2.5em;
        left: 0;
        bottom: 0;
        width: 100%;
        max-width: 38em;
        @include grid100gap(1.5em);
        z-index: 3;
    }
    &__title{
        color: #fff;
    }
    &__tags{
        display: flex;
        flex-wrap: wrap;
    }
}

.measure-modal{
    .modal__dialog{
        width: 84.5em;
    }
    &__info{
        margin-top: -2.75em;
        @include grid100gap(1.25em);
    }
    .measure__items{
        gap: 2em;
    }
}

.call-modal{
    .modal__dialog{
        width: 67em;
    }
    &__grid{
        display: grid;
        grid-template-columns: 26.25em calc(100% - 28.2825em);
        gap: 2em;
    }
    &__info{
        @include grid100gap(1.25em);
    }
    .consult__socials{
        justify-content: unset;
    }
    &__master{
        margin-top: -2.75em;
    }
    .measure__items{
        gap: 1.5em;
    }
}

.master{
    position: relative;
    &__img{
        @include imgCover(175%);
        img{
            border-radius: .375em;
            display: block;
        }
    }
    .ceo-info{
        width: 15.9375em;
    }
}

.download-modal{
    @media screen and (min-width: 993px){
        .close-btn{
            background-color: #fff;
            padding: .25em 1em;
            margin-right: -1em;
        }
    }
    .modal__dialog{
        width: 77.125em;
    }
    &__grid{
        display: grid;
        grid-template-columns: 30.625em calc(100% - 30.625em);
    }
    &__img{
        width: calc(100% + 8.75em);
        margin: -5.5em 0 0 -6.25em;
        img{
            width: 100%;
            display: block;
        }
    }
    &__media{
        position: relative;
        .catalog-contain{
            position: absolute;
            right: 0;
            bottom: 0;
        }
    }
}

.hint-modal{
    &__grid{
        @include grid100gap(1.5em);
    }
    @media screen and (min-width: 577px){
        .close-btn{
            display: none;
        }
    }
}

._full-grid{
    grid-column: 1/-1;
}

@media screen and (max-width: 992px){
    .modal{
        display: block;
        &__dialog{

        }
    }

    .same-modal{
        &__grid{
            grid-template-columns: 100%;
        }
        &__calc{
            margin: 0;
        }
        &__variant{
            display: none;
        }
    }

    .measure-modal{
        &__master{
            display: none;
        }
        &__info{
            margin: 0;
        }
    }

    .call-modal{
        &__grid{
            grid-template-columns: 100%;
        }
        &__master{
            display: none;
        }
    }

    .download-modal{
        &__grid{
            grid-template-columns: 100%;
        }
        &__media{
            display: none;
        }
    }
}


@media screen and (max-width: 576px) {
    .modal {
        &__dialog {
            padding-left: 1em;
            padding-right: 1em;
        }
    }
}
