@import "../mixins";
.sticky-menu{
  position: sticky;
  z-index: 10;
  top: 0;
  padding: 1.5em;
  background-color: #fff;
  &__content{
    position: relative;
  }
  &__wrap{
    height: 100%;
    right: 0;
    top: 5em;
    position: absolute;
    width: 20.375em;
    padding: 0 0 0 5em;
  }
  &__items{
    @include grid100gap(2em);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: .625em;
      transform: translateX(-50%);
      width: 1px;
      height: 100%;
      background-color: $red;
    }
  }
  &__item{
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
    &._active{
      font-weight: 700;
      .sticky-menu__marker{
        background-color: $red;
      }
    }
    &-text{
      font-size: 1em;
      line-height: 120%;
      text-shadow:
              0 0 7px #fff,
              0 0 10px #fff,
              0 0 21px #fff,
              0 0 42px #fff,
              0 0 82px #fff,
              0 0 92px #fff,
              0 0 102px #fff,
              0 0 151px #fff;
    }
  }
  &__marker{
    background-color: #fff;
    width: 1.25em;
    height: 1.25em;
    border-radius: 50%;
    border: 1px solid $red;
    flex-shrink: 0;
    margin-right: 1em;
  }
  &__close{
    display: flex;
    align-items: center;
    margin-top: 1.5em;
    svg{
      width: 1.25em;
      height: auto;
      margin-right: 1em;
    }
    span{
      font-size: 1em;
    }
    &:hover{
      color: $red;
    }
  }
}

@media screen and (max-width: 992px){
  .sticky-menu{
    &__wrap{
      display: none;
    }
  }
}
