@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-weight: 500;
    border-radius: .375em;
    height: 3.9375em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-green{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-red{
    background-color: $red;
    color: #fff;
    &:hover{
        background-color: darken($red,10%);
        color: #fff;
    }
}

.m-btn-black{
    background-color: $black;
    color: #fff;
    &:hover{
        background-color: darken($black,10%);
        color: #fff;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    color: $primary;
    &:hover{
        color: darken($primary,10%);
        path{
            stroke:darken($primary,10%);
        }
    }
    span{
        font-size: 1em;
        line-height: 150%;
    }
    svg{
        width: 1.5em;
        height: auto;
        margin-left: .375em;
    }
}

.swiper-button{
    width: 1.5em;
    height: auto;
    img{
        width: 100%;
    }
    &-disabled{
        opacity: .4;
    }
    &._type2{
        width: 3em;
        height: 3em;
        background-color: $red;
        border: 1px solid $red;
        border-radius: 50%;
        svg{
            width: 1.5em;
            height: auto;
        }
        &.swiper-button-disabled{
            opacity: 1;
            background-color: transparent;
            path{
                stroke: $red;
            }
        }
    }
}
