@import "../mixins";
.advantage-card{
  padding: 1em;
  background: rgba(255, 255, 255, 0.74);
  backdrop-filter: blur(6px);
  border-radius: .375em;
  display: flex;
  &__icon{
    flex-shrink: 0;
    margin-right: .75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    a{
      color: $red;
      text-decoration: underline;
      &:hover{
        text-decoration: none;
      }
    }
  }
}

.discount-card{
  padding: 2em;
  background: #FFFFFF;
  border-radius: .375em;
  &__desc{
    margin-top: 1.25em;
  }
}

.calc-card{
  &__img{
    @include imgCover(61.7%);
    img{
      border-radius: .375em;
    }
  }
  &__top{
    margin-top: .625em;
    padding-left: .1875em;
    display: flex;
  }
  &__hint{
    padding-top: .0625em;
    margin-left: .5em;
    flex-shrink: 0;
    &-toggle{
      width: .9375em;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }
  &__by{
    margin-top: .625em;
    display: flex;
    flex-direction: column;
  }
  &__square{
    color: $gray2;
    margin-top: .625em;
  }
}

.count-card{
  .cartcalc{
    margin-top: 1.25em;
    max-width: 9.0625em;
  }
}

.solution-card{
  padding: 1.5em;
  background: #FFFFFF;
  border-radius: .375em;
  display: flex;
  flex-direction: column;
  &__slider{
    overflow: hidden;
    position: relative;
    .swiper{
      &-pagination{
        position: absolute;
        bottom: 1em;
      }
    }
  }
  &__img{
    @include imgCover(75.2%);
    img{
      border-radius: .375em;
    }
  }
  &__content{
    flex-grow: 1;
    margin-top: 1.5em;
    @include grid100gap(1.5em);
  }
  &__tags{
    margin-top: -1.25em;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .25em);
    .tag{
      margin-top: 1em;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.5em;
  }
  &__btn{
    width: 100%;
  }
}

.canvas-card{
  background-size: cover;
  background-position: center;
  padding: 1.5em;
  border-radius: .375em;
  color: #fff;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    height: 3.375em;
    display: flex;
    align-items: center;
    img{
      max-height: 100%;
      max-width: 100%;
      display: block;
    }
  }
  &__sale{
    flex-shrink: 0;
    width: 4.5em;
    height: 4.5em;
    border-radius: 50%;
    background-color: $red;
    color: #fff;
    font-weight: 700;
    @include center;
    text-align: center;
    span{
      transform: matrix(0.97, 0.24, -0.22, 0.98, 0, 0);
    }
  }
  &__info{
    margin-top: .625em;
    @include grid100gap(.625em);
  }
  &__bottom{
    padding-top: 8.75em;
    margin-top: auto;
  }
  &__props{
    @include grid100gap(.375em);
  }
  &__price{
    margin-top: 1.5em;
    @include grid100gap(.625em);
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
    .m-btn{
      padding: 0 2.5em;
    }
  }
}

.certificate-card{
  &__img{
    display: block;
    @include imgCover(144.6%);
    margin-bottom: 1.5em;
    img{
      display: block;
      border-radius: .375em;
    }
  }
  &__title{
    max-width: 14.4375em;
  }
}

.light-card{
  display: block;
  &__img{
    display: block;
    padding-bottom: 100%;
    position: relative;
    background: #F4F5F8;
    border-radius: .375em;
    margin-bottom: 1.5em;
    img{
      @include centerAbsolute;
      max-width: calc(100% - 6em);
      max-height: calc(100% - 6em);
    }
  }
}

.team-card{
  &__img{
    @include imgCover(141.9%);
    img{
      border-radius: .375em;
      display: block;
    }
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(.625em);
  }
}

.service-card{
  position: relative;
  color: #fff;
  &__img{
    @include imgCover(67.7%);
    img{
      display: block;
      border-radius: .375em;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 2;
      background: linear-gradient(0deg, rgba(34, 34, 34, 0.5), rgba(34, 34, 34, 0.5));
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      border-radius: .375em;
    }
  }
  &__content{
    position: absolute;
    padding: 2.5em;
    @include grid100gap(.625em);
    width: 100%;
    left: 0;
    bottom: 0;
    z-index: 2;
  }
}

.news-card{
  display: block;
  &__img{
    @include imgCover(67.7%);
    img{
      display: block;
      border-radius: .375em;
    }
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(.75em);
  }
}

.article-card{
  position: relative;
  &__play{
    position: absolute;
    width: 2.5em;
    top: 1.5em;
    left: 1.5em;
    z-index: 2;
  }
  .news-card__img{
    padding-bottom: 56.2%;
  }
  &__views,
  &__bottom{
    display: flex;
    align-items: center;
  }
  &__views{
    margin-left: 1em;
    img{
      margin-right: .5em;
      width: 1.5em;
      display: block;
    }
  }
}

.qa-card{
  &._active{
    .qa-card__btn{
      transform: rotate(180deg);
    }
  }
  &__show{
    padding: 1.125em 1.5em;
    background-color: $grayBg;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__hidden{
    padding: 1.5em 1.5em 0;
    display: none;
  }
}

.contact-group{
  &__text{
    @include grid100gap(.625em);
  }
  &__socials{
    display: flex;
    margin-top: 1.5em;
    .consult__social{
      margin: 0 .75em 0 0;
    }
  }
}

.place-card{
  max-width: 16.6875em;
  &__inner{
    @include grid100gap(.75em);
  }
}


@media screen and (max-width: 992px){
  .solution-card{
    padding: .75em;
    &__img{
      padding-bottom: 88.15%;
    }
    &__desc{
      font-size: 1.125em;
    }
  }

  .canvas-card{
    .display3{
      font-size: 2.25em;
    }
    .text20{
      font-size: 1.125em;
    }
  }

  .certificate-card{
    &__img{
      margin-bottom: 1em;
    }
    &__title{
      max-width: 100%;
    }
  }

  .service-card{
    &__content{
      padding: 1em;
      gap: .5em;
    }
  }

  .contact-group{
    &__socials{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .calc-card{
    .m-check{
      span{
        font-size: .875em;
      }
      label{
        &:before{
          margin-right: .4375em;
        }
      }
    }
  }

  .discount-card{
    padding: 1em;
  }

  .canvas-card{
    padding: 1em;
    &__top{
      align-items: unset;
    }
    &__logo{
      height: 2.5em;
    }
    &__info{
      margin-top: -.5em;
      gap: .25em;
    }
    &__bottom{
      padding-top: 5em;
    }
    &__nav{
      .m-btn{
        width: 100%;
        padding: 0;
      }
    }
  }
}
