.display1,
.display2,
.display3,
.display4{
  font-weight: 300;
  line-height: 120%;
}

.display1{
  font-size: 3.5em;
}

.display2{
  font-size: 3em;
}

.display3{
  font-size: 2.5em;
}

.display4{
  font-size: 2em;
}

.text24{
  font-size: 1.5em;
  line-height: 120%;
}

.text20{
  font-size: 1.25em;
  line-height: 125%;
}

.marker-list li,
.m-list li,
.fg label,
.text16{
  font-size: 1em;
  line-height: 150%;
}

.text14{
  font-size: .875em;
  line-height: 150%;
}

.m-list{
  padding-left: .625em;
  li{
    position: relative;
    padding-left: .875em;
    &:before{
      content: '';
      position: absolute;
      width: .1875em;
      height: .1875em;
      background-color: $black;
      border-radius: 50%;
      left: 0;
      top: .5625em;
    }
  }
}

.marker-list{
  li{
    position: relative;
    padding-left: 2.125em;
    &:before{
      content: '';
      position: absolute;
      width: 1.5em;
      height: 1.5em;
      left: 0;
      top: 0;
      background-image: url(../img/check2.svg);
      @include bgImgSize(100%);
    }
  }
}


.bold-text,
b{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.regular-text{
  font-weight: 400;
}

.gray-text2{
  color: $gray2;
}

.green-text{
  color: $primary;
}


@media screen and (max-width: 992px){
  .display3,
  .display4{
    font-size: 1.5em;
  }

  .display1,
  .display2,
  .display2-mob{
    font-size: 1.875em;
  }

  .text24{
    font-size: 1.25em;
  }

  .text20{
    font-size: 1.125em;
  }

  .marker-list li,
  .text16{
    font-size: .875em;
  }


  .marker-list{
    li{
      padding-left: 2.43em;
      padding-top: .14em;
      &:before{
        width: 1.71em;
        height: 1.71em;
      }
    }
  }
}
